:root {
    --primary: #ff5674;
    --greyed: #8b8b8b;
  }
.App {
  text-align: center;
}
#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.navbarframe{
    border: 0.5px solid rgba(0,0,0,0.08);
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}
.searchbarframe{
    display:none;
    position: relative;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
}
.searchbar{
    
    border: 0.5px solid rgba(0,0,0,0.08);
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:0.5rem 1rem;
    border-radius:50px;
    gap:1rem;
    font-weight: 650;
    position: relative;
    z-index: 2;
}
.searchbar>div:hover{
    transform:scale(1.02);
    cursor:pointer;
}
.exchangeOption1Menu{
    z-index: 1;
    width:45%;
    position: absolute;
    bottom: -11rem;
    left: 0;
    background-color: white;
    border-radius: 0px 0px 15px 15px;
    border: 0.5px solid rgba(0,0,0,0.08);
    border-top: none;
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);


    transition: height 0.45s ease-in-out;
    overflow: hidden;
    top: 100%;
    left: 0;
    height:0;
}
.exchangeOption1Menu>div>div{
    padding:0.3rem;
}
.homeHero1{
    position: relative;
    padding-bottom:10rem!important;
    background-image: url(https://exchanger.gg/assets/images/overlay-1.svg) !important;
}
.headerhome1{
    text-align:center;
}
.headerhome1>span{
    font-size:1.85rem;
    font-weight:900;
    color:var(--primary);
}
.homeheroofferselector{
    padding:1rem;
    border-radius:15px;
    position: absolute;
    top:60%;
    background-color: white;
    border: 0.5px solid rgba(0,0,0,0.08);
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}
.activeHomeBtn1{
    background: #ff92a5!important;
    color: white;
}
.activeMarketBtn1{
    background: #ff92a5!important;
    color: white;
}
.selectPay1{
    position: relative;
    justify-content: space-between;
    border-radius:50px;
    -webkit-padding-start: var(--chakra-space-4);
    padding-inline-start: var(--chakra-space-4);
    -webkit-padding-end: var(--chakra-space-4);
    padding-inline-end: var(--chakra-space-4);
    height: var(--chakra-sizes-10);
    border: 1px solid;
    border-color: inherit;
    background: inherit;
    outline: 2px solid transparent;
    outline-offset: 2px;
    width:100%;
    align-items:center;
    
}
.selectPay1:hover{
    cursor:pointer;
}
.selectPay1Box {
    display: none;
    height: 0;
    overflow: hidden;
    transition: height 0.45s ease-in-out; /* Make sure this line is included */
    position: absolute;
    top: 100%;
    left: 0;
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    border-radius: 15px;
    flex-direction: column;
    z-index: 1;

  }
  .selectPay1Box>div{

    padding: 0.3rem 0.7rem;
  }
.selectPay1Box>div:hover{
    cursor:pointer;
}
.arrowdownselectpay{
    transition: transform 0.45s ease-in-out; 
}


.PayWithFrame{
    align-items: center;
    justify-content: space-between;
    width:100%;
    border-radius:50px;
    -webkit-padding-start: var(--chakra-space-4);
    padding-inline-start: var(--chakra-space-4);
    -webkit-padding-end: var(--chakra-space-4);
    padding-inline-end: var(--chakra-space-4);
    height: var(--chakra-sizes-10);
    border: 1px solid;
    border-color: inherit;
    background: inherit;
    outline: 2px solid transparent;
    outline-offset: 2px;
}

.PayWithFrame>p:nth-child(2){
    border: 0.5px solid rgba(0,0,0,0.08);
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    border-radius:50px;
    padding: 0.1rem 0.4rem;
   
}
.selectionsFrame1{
    gap:0.4rem;
    max-width:236px;
    margin-top:0.3rem;
}
.selectionsFrame1>p{
    font-size:0.8rem;
    border: 0.5px solid rgba(0,0,0,0.08);
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:0.1rem 0.3rem;
    border-radius:50px;
    flex-shrink:0;
}
.marketSideMenu{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:1.5rem;
    position:relative;
}

.selectPay2{
    position: relative;
    justify-content: space-between;
    border-radius:50px;
    -webkit-padding-start: var(--chakra-space-4);
    padding-inline-start: var(--chakra-space-4);
    -webkit-padding-end: var(--chakra-space-4);
    padding-inline-end: var(--chakra-space-4);
    height: var(--chakra-sizes-10);
    border: 1px solid;
    border-color: inherit;
    background: white;
    outline: 2px solid transparent;
    outline-offset: 2px;
    width:100%;
    align-items:center;
    
}
.selectPay2:hover{
    cursor:pointer;
}
.selectPay2Box {
    display: none;
    height: 0;
    overflow: hidden;
    transition: height 0.45s ease-in-out; /* Make sure this line is included */
    position: absolute;
    top: 100%;
    left: 0;
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    border-radius: 15px;
    flex-direction: column;
    z-index: 1;

  }
  .selectPay2Box>div{

    padding: 0.3rem 0.7rem;
  }
.selectPay2Box>div:hover{
    cursor:pointer;
}

/* Side Menu on market*/
.findOffersMarketFrame{
    display:none!important;
    bottom:0;
    width:100%;
}
.findOffersMarket{
    background-color:var(--primary);
    width:100%;

}
.listOffersTop{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}
.listOffersTop>p{
    font-size:0.8rem;
    font-weight: 600;
}
.listOffersBottom{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}
.thumbsdown{
    transform: rotate(180deg) scaleX(-1);
}
.thumbsdown>g>path{
    fill:#C64B4B!important;
}



.hidden{
    display:none;
}

.profiletop{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:1rem;
    border-radius:15px;
    width:100%;
}
.profileside1{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:1rem;
    border-radius:15px;
    overflow: hidden;
}
.profileside2{
    box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    padding:1rem;
    border-radius:15px;
    max-height:56px;
}

.profileStat1{
    color:var(--greyed)!important;
    font-weight: 600;
}
.profileStat1>span{
    color:black!important;
}

.sliderForTab1{
    
  transition: 0.3s ease-in;
}

#root>div:nth-child(2){
    margin-top:78.05px;
    z-index: 1;
}
.navbarframe{
    z-index: 3;
    background: white;
    position: fixed!important;
}

@media screen and (max-width: 730px){
    .profileBottom{
        flex-direction: column;
    }
    .profileside1{
            width:100%!important;
    }
    .profileside2Frame{
        width:100%!important;
    }
}